<template>
	<div class="body">
		<div class="company_name flex_between">
			<div class="company_name_left">
				<i class="icon iconfont icon-gongsixiangqinglogo"></i>
				<span>新增项目</span>
			</div>
		</div>
		<div class="form_box">
			<div class="form flex_between form_bottom">
				<div class="form_list flex_start">
					<div class="form_list_left">
						项目名称
					</div>
					<div class="form_list_right">
						<el-input placeholder="请填写" v-model="commitData.name" clearable>
						</el-input>
					</div>
				</div>
				<div class="form_list flex_start">
					<div class="form_list_left">
						项目企业名称
					</div>
					<div class="form_list_right">
						<!-- <el-input placeholder="请填写" v-model="commitData.company_name" clearable>
						</el-input> -->
						<el-select  v-model="tianyan_id" filterable remote :clearable='true' reserve-keyword placeholder="请填写" :remote-method="remoteMethod" :loading="loading" @change="getProjectCompany($event)">
							<el-option v-for="option in projectCompanyList"  :key="option.id" :label="option.name" :value="option.id"  >
							</el-option>
						</el-select>
					</div>
				</div>
				<!-- <div class="form_list flex_start">
					<div class="form_list_left">
						币种
					</div>
					<div class="form_list_right">
						<el-select v-model="commitData.unit" clearable placeholder="请选择">
							<el-option v-for="item in moneyTypeList" :key="item.id" :label="item.name" :value="item.id">
							</el-option>
						</el-select>
					</div>
				</div> -->
				<!-- <div class="form_list flex_start">
					<div class="form_list_left">
						认购子基金名称
					</div>
					<div class="form_list_right">
						<el-select v-model="commitData.subscribe_fund_ids" clearable filterable multiple placeholder="请选择" class="subFundProjectAddSelect">
							<el-option v-for="item in subscribeFundList" :key="item.id" :label="item.name" :value="item.id">
							</el-option>
						</el-select>
					</div>
				</div> -->
				<div class="form_list flex_start">
					<div class="form_list_left">
						行业
					</div>
					<div class="form_list_right">
						<el-input placeholder="请填写" v-model="commitData.industry" clearable>
						</el-input>
					</div>
				</div>
				<div class="form_list flex_start">
					<div class="form_list_left">
						数据创建人
					</div>
					<div class="form_list_right">
						<el-select v-model="commitData.create_user_id" clearable  filterable placeholder="请选择">
							<el-option v-for="item in creatorList" :key="item.id" :label="item.name" :value="item.id">
							</el-option>
						</el-select>
					</div>
				</div>
				<div class="form_list flex_start">
					<div class="form_list_left">
						是否舆情监测
					</div>
					<div class="form_list_right flex_start">
						<div @click="changeHideStatus(1)" class="radio_btn cursor" :class="[commitData.is_monitor?'radio_btn_active':'']">
							是
						</div>
						<div @click="changeHideStatus(2)" class="radio_btn cursor" :class="[!commitData.is_monitor?'radio_btn_active':'']">
							否
						</div>
					</div>
				</div>
				<div class="form_list flex_start">
					<div class="form_list_left">
						项目简介
					</div>
					<div class="form_list_right form_list_right_textarea">
						<el-input placeholder="请填写"  type="textarea" :rows="3" v-model="commitData.introduce">
						</el-input>
					</div>
				</div>
				
			</div>

			<!-- 列表数据 -->
			<div class="form flex_between form_bottom" v-for="(item,index) of commitData.quote_infos" :key="index">
				<div class="form_list flex_start">
					<div class="form_list_left">
						项目企业股票代码
					</div>
					<div class="form_list_right">
						<el-input placeholder="请填写" v-model="item.share_code" clearable>
						</el-input>
					</div>
				</div>
				<div class="form_list flex_start">
					<div class="form_list_left">
						上市/挂牌板块
					</div>
					<div class="form_list_right">
						<el-select v-model="item.list_plate_id" clearable filterable placeholder="请选择">
							<el-option v-for="item in plateList" :key="item.id" :label="item.name" :value="item.id">
							</el-option>
						</el-select>
					</div>
				</div>
				<div class="form_list flex_start">
					<div class="form_list_left">
						上市时间
					</div>
					<div class="form_list_right">
						<el-date-picker v-model="item.quoted_date" type="date" value-format="yyyy-MM-dd" placeholder="选择日期" :editable='false'>
    					</el-date-picker>
					</div>
				</div>
				<div class="form_list flex_start">
					<span @click="deleteData(index)" v-if="commitData.quote_infos.length>1">删除</span>
					<div class="table-group-title-btn btn-blue-border" @click="addData()" v-if="index==commitData.quote_infos.length-1" >
						 &nbsp;&nbsp;新增数据&nbsp;&nbsp;
					</div>
				</div>
			</div>
			<!-- <div class="form flex_between form_bottom" >
				<div class="form_list flex_start">
					<div class="form_list_left">
						项目企业股票代码
					</div>
					<div class="form_list_right">
						<el-input placeholder="请填写" v-model="quote_infos_obj.share_code" clearable>
						</el-input>
					</div>
				</div>
				<div class="form_list flex_start">
					<div class="form_list_left">
						上市/挂牌板块
					</div>
					<div class="form_list_right">
						<el-select v-model="quote_infos_obj.list_plate_id" clearable filterable placeholder="请选择">
							<el-option v-for="item in plateList" :key="item.id" :label="item.name" :value="item.id">
							</el-option>
						</el-select>
					</div>
				</div>
				<div class="form_list flex_start">
					<div class="form_list_left">
						上市时间
					</div>
					<div class="form_list_right">
						<el-date-picker v-model="quote_infos_obj.quoted_date" type="date" value-format="yyyy-MM-dd" placeholder="选择日期">
    					</el-date-picker>
					</div>
				</div>
				<div class="form_list flex_start">
					<span @click="deleteData(index)" v-if="index!=commitData.quote_infos.length-1" >删除</span>
                    <button class="flex_center" @click="addData()" >添加</button>
				</div>
			</div> -->
		</div>
		
		<!--表单按钮模块-->
		<div class="form_submit flex_center">
			<button @click="cancleAddSubFundProject()">取消</button>
			<button @click="toAddSubFundProject()">保存</button>
		</div>
	</div>
</template>

<script>
	import {getUserlistApi,getcomPany,} from '../../config/api'
	import {getChildData,getQuotedPlace,addFundProject} from '../../config/apiSj'
	import {dialogConfirm,dialogConfirmHasbtn} from '../../config/mUtils'
	export default {
		data() {
			return {
				projectCompanyList:[],//项目企业名称列表
				loading: false,
				tianyan_id:'',//天眼查id

				// subscribeFundList: [],//认购子基金列表
				plateList:[],//上市/挂牌板块列表
				moneyTypeList:[
					{
						id:1,
						name:'人民币'		
					},
					{
						id:2,
						name:'美元'		
					},
				],//币种列表
				creatorList:[],//数据创建人列表

				
				commitData:{
					name:'',
					company_name:'',
					// unit:'',
					// subscribe_fund_ids:'',
					industry:'',
					introduce:'',
					tianyan_id:'',
					quote_infos:[
						{
							share_code:'',
							quoted_date:'',
							list_plate_id:'',
						}
					],
					create_user_id:'',
					is_monitor:1,
				},//提交的数据
			}
			
		},
		created() {
			this.getModuleInfo();
			// this.getFundList();
			this.getAllUserList();
		},
		mounted() {},
		components: {},
		methods: {
			//本地操作方法
			changeHideStatus(e){
				if (e == 1) {
					this.commitData.is_monitor = 1;
				} else{
					this.commitData.is_monitor = 0;
				}
			},

			//获取上市/挂牌模板信息
			getModuleInfo(){
				//请求接口
				getQuotedPlace({
					keyword:'',
				}).then(res => {
					// console.log(res);
					if (res.data.msgNo == 1) {
						// dialogConfirm('success', res.data.message);
						// console.log(res.data)
						let data = res.data.payload ;
						this.plateList = data;
					}else{
						// dialogConfirm('info', res.data.message);
					}
				})
			},

			//获取认购子基金列表
			// getFundList(){
			// 	//请求接口
			// 	getChildData().then(res => {
			// 		// console.log(res);
			// 		if (res.data.msgNo == 1) {
			// 			// dialogConfirm('success', res.data.message);
			// 			// console.log(res.data)
			// 			let data = res.data.payload ;
			// 			this.subscribeFundList = data;
			// 		}else{
			// 			// dialogConfirm('info', res.data.message);
			// 		}
					
			// 	})
			// },

			// 获取数据创建人列表
			getAllUserList(){
				getUserlistApi({
					number: 10000,
					page: 1
				}).then(res => {
					if(res.data.msgNo == 1) {
						this.creatorList = res.data.payload.data;
						this.commitData.create_user_id = parseInt( window.localStorage.getItem('htcx_uid')) ;
					}
				})
			},

			//添加项目企业挂牌信息
			addData(){
				this.commitData.quote_infos.push({
					share_code:'',
					quoted_date:'',
					list_plate_id:'',
				});
				// this.quote_infos_obj = {
				// 	share_code:'',
				// 	quoted_date:'',
				// 	list_plate_id:'',
				// }
			},

			//添加项目企业挂牌信息
			deleteData(index){
				this.commitData.quote_infos.splice(index,1);
			},

			//取消添加子基金项目
			cancleAddSubFundProject(){
				this.$router.push({
					name: 'subFundProjectHome',
				})
			},

			//添加子基金项目
			toAddSubFundProject(){
				// //处理 基金期限（年）的数据格式
				// let fund_end_date = JSON.parse(JSON.stringify(this.fund_end_date));
				// fund_end_date.forEach((element,index) => {
				// 	if(!element){
				// 		fund_end_date.splice(index,1);
				// 	}
				// });
				// this.commitData.fund_end_date = fund_end_date.join(',');

				
				//处理数据
				let commitData = JSON.parse(JSON.stringify(this.commitData));
				// commitData.subscribe_fund_ids = commitData.subscribe_fund_ids.join('|');
				
				if(commitData.quote_infos.length>0){
					commitData.quote_infos = JSON.stringify(commitData.quote_infos);
				}
				// console.log(commitData);
				// return false;
				//请求接口
				if (this.commitData.company_name) {
					addFundProject(commitData).then(res => {
						if (res.data.msgNo == 1) {
							let data  = res.data.payload;
							dialogConfirm('success', '保存成功');
							//跳转列表页面
							this.$router.push({
								name: 'subFundProjectHome',
							})
						}else{
							dialogConfirm('info', res.data.message);
						}
					})
				} else{
					dialogConfirm('info', '项目企业名称不能为空');
				}
			},



			//请求天眼查
			remoteMethod(query) {
				if(query !== '') {
					this.loading = true;
					getcomPany({
						name: query
					}).then(res => {
						this.loading = false;
						if(res.data.msgNo == 1) {
							if(res.data.payload.length == 0) {
								this.projectCompanyList = [{
									name: query,
									tianyan_id:0,
								}]
							} else {
								let newObj = {
									name: query,
									tianyan_id:0,
								}
								res.data.payload.push(newObj);
								this.projectCompanyList = res.data.payload;
							}

						}
					})
				} else {
					this.projectCompanyList = [];
				}
			},
			getProjectCompany(e){
				// console.log(e);
				// console.log(this.projectCompanyList);
				this.projectCompanyList.forEach((element,index) => {
					if(element.id==e){
						this.commitData.tianyan_id = element.id;
						this.commitData.company_name = element.name;
					}
				});
			}
			
		},
	}
</script>

<style lang="scss" scoped="scoped">
	.body {
		padding: 0 30px 30px 30px;
		font-size: 14px;
	}
	.company_name {
		height: 72px;
		border-bottom: 1px solid #EAEAEA;
	}
	.company_name_left {
		font-size: 18px;
		color: #33302D;
	}
	.company_name_left i {
		margin-right: 3px;
		color: #6292F4;
	}
	.form_box{
		// padding: 30px 0;
	}
	.form{
		flex-wrap: wrap;
		color: #6F7071;
		padding: 30px 0 10px;
	}
	.form_bottom{
		border-bottom:1px solid #E5E5EA;
	}
	.form_list{
		display: inline-flex;
		margin-bottom: 20px;
		&>span{
			font-size: 14px;
			color: #6292F4;
			cursor: pointer;
		}
		&>button{
			// width: 105px;
			// height: 40px;
			// border-radius: 20px;
			// // background: #6292F4;
			// color: #6292F4;
			// font-size: 14px;
			// cursor: pointer;
			// margin-left: 46px;
			// border: 1px solid #6292F4;
			width: 100px;
			height: 30px;
			color: #6292F4;
			font-size: 14px;
			border-radius: 15px;
			background-color: white;
			margin-left: 46px;
			border: 1px solid #6292F4;
		}
		// &>button:hover{
		// 	color: white;
		// 	background-color: #6292F4;
		// }
	}
	.form_list_left{
		width: 130px;
		color: #6F7071;
	}
	.form_list_right{
		width: 260px;
    	// height: 40px;
	}
	.form_list_right_textarea{
		width: 800px;
	}
	.radio_btn{
		width: 73px;
		height: 40px;
		margin-right: 38px;
		line-height: 40px;
		text-align: center;
		border-radius: 20px;
		background: #F3F6FB;
	}
	.radio_btn_active{
		color: white;
		background: #6292F4 !important;
	}
	.form_submit{
		margin-top: 40px;
	}
	.form_submit button{
		width: 94px;
		height: 36px;
		color: #6292F4;
		font-size: 14px;
		margin-right: 26px;
		border-radius: 20px;
		border: 1px solid #6292F4;
		
	}
	.form_submit button:nth-child(2){
		color: white;
		background: #6292F4;
	}
	// .subFundProjectAddSelect .el-select__tags{
	// 	z-index: -1 !important;
	// }
</style>


